// $lib/api.js
import { get } from 'svelte/store';
import { accessToken } from '$lib/stores';

export default class ApiClient {
    constructor({
        baseURL = '',
        withCredentials = false,
        defaultHeaders = { 'Content-Type': 'application/json' },
        defaultResponseType = 'json'
    } = {}) {
        this.baseURL =
            import.meta.env.VITE_API_BASE_URL ||
            baseURL ||
            `${location.protocol}//${location.host}`;
        this.withCredentials = withCredentials;
        this.defaultHeaders = defaultHeaders;
        this.defaultResponseType = defaultResponseType;
    }

    //  Build query t object
    buildQuery(params = {}) {
        const q = new URLSearchParams(params).toString();
        return q ? `?${q}` : '';
    }

    async request(endpoint, {
        method = 'GET',
        data = null,
        params = null,    // <--  h tr params object
        headers = {},
        responseType = this.defaultResponseType,
        withCredentials = this.withCredentials
    } = {}) {
        //  build URL + params
        const url = `${this.baseURL}${endpoint}${params ? this.buildQuery(params) : ''}`;

        //  ly accessToken t Svelte store
        const token = get(accessToken);

        const options = {
            method,
            credentials: withCredentials ? 'include' : 'same-origin',
            headers: {
                ...(data instanceof FormData ? {} : this.defaultHeaders),
                ...(token ? { Authorization: `Bearer ${token}` } : {}), // <--  t attach Token
                ...headers
            }
        };

        if (data) {
            options.body = data instanceof FormData ? data : JSON.stringify(data);
        }

        const res = await fetch(url, options);
        if (!res.ok) throw new Error(`HTTP ${res.status}: ${res.statusText}`);

        switch (responseType) {
            case 'json': return await res.json();
            case 'text': return await res.text();
            case 'blob': return await res.blob();
            case 'arrayBuffer': return await res.arrayBuffer();
            case 'formData': return await res.formData();
            default: return res;
        }
    }

    // helper methods
    get(endpoint, opts = {}) {
        return this.request(endpoint, { ...opts, method: 'GET' });
    }

    //  GET c nhiu params: { page: 1, size: 20 }
    getParams(endpoint, params = {}, opts = {}) {
        return this.request(endpoint, { ...opts, method: 'GET', params });
    }

    post(endpoint, data, opts = {}) {
        return this.request(endpoint, { ...opts, method: 'POST', data });
    }

    put(endpoint, data, opts = {}) {
        return this.request(endpoint, { ...opts, method: 'PUT', data });
    }

    delete(endpoint, opts = {}) {
        return this.request(endpoint, { ...opts, method: 'DELETE' });
    }
}

// Instance mc nh
export const api = new ApiClient();

// Special helpers
export async function postImage(blob) {
    const formData = new FormData();
    formData.append("image", blob, "capture.jpg");
    return api.post("/capture", formData);
}

export async function streamInput(chunk, isLast = false) {
    const formData = new FormData();
    formData.append("chunk", chunk, isLast ? "final.webm" : "chunk.webm");
    formData.append("stop", isLast.toString());
    return api.post("/stream-input", formData);
}
