<script>
  import { onMount, onDestroy } from 'svelte';
  import { user, accessToken, loading } from '$lib/stores';

  import Home from '@routes/Home.svelte';
  import Login from '@routes/Login.svelte';
  import Loading from '@components/loading/index.svelte';
  import { connectWebSocket } from '$lib/websocket-client.js';
  import { initTheme } from '$lib/theme';

  import ApiClient from '$lib/api';
  const api = new ApiClient();
  let hasWarned = false;

  onMount(async () => {
    initTheme();
    connectWebSocket();

    window.addEventListener('beforeunload', handleBeforeUnload);
    window.addEventListener('popstate', handleGoBack);
    loading.set(true);
    try {
      const mode = import.meta.env.MODE;
      const isDev = import.meta.env.DEV;
      const isProd = import.meta.env.PROD;
      // console.log(
      //   `App running in: ${mode} mode. DEV: ${isDev}, PROD: ${isProd}`,
      //   'url:',
      //   import.meta.env.VITE_API_BASE_URL
      // );

      const data = await api.get('/auth/me', { withCredentials: true });
      if (data.user) {
        user.set(data.user);
        accessToken.set(data.accessToken);
      } else {
        user.set(null);
        accessToken.set(null);
      }
    } catch (e) {
      console.error('AUTH ERROR:', e);
      user.set(null);
      accessToken.set(null);
    } finally {
      loading.set(false);
    }
  });

  onDestroy(() => {
    window.removeEventListener('beforeunload', handleBeforeUnload);
    window.removeEventListener('popstate', handleGoBack);
  });

  //  Hm cnh bo khi reload hoc ng tab
  function handleBeforeUnload(event) {
    if ($user) {
      event.preventDefault();
      event.returnValue = ''; // cn dng ny  trnh duyt hin th hp thoi xc nhn
    }
  }

  //  Hm cnh bo khi ngi dng nhn nt "Back"
  function handleGoBack(event) {
    if ($user && !hasWarned) {
      const confirmLeave = confirm('Bn c chc mun ri khi ng dng khng?');
      if (!confirmLeave) {
        // y li trng thi hin ti  chn quay li
        history.pushState(null, '', window.location.href);
      } else {
        hasWarned = true;
        history.back(); // cho php i ngc li sau khi ng 
      }
    }
  }

  //$: console.log('log1:', $user, $accessToken);
</script>

{#if $loading}
  <Loading />
{:else if $user}
  <Home />
{:else}
  <Login />
{/if}
